import { useEffect, useRef,useState, memo } from "react";
import styles from "./technology.module.css";

const Technology = ({scroll, SideScroller, flag, root,onSections }) => {
  const bannerRef = useRef();
  const techRef = useRef();
  const istackRef = useRef();
  const virtualRef = useRef();
  const wifiRef = useRef();
  const wtcpRef = useRef();
  const tcpRef = useRef();
  const [arrowDot, setArrowDot] = useState([]);

  useEffect(() => {
    window.scrollTo({top:0,behavior:"auto"});
    const sections = [
      bannerRef.current,
      techRef.current,
      istackRef.current,
      virtualRef.current,
      wifiRef.current,
      wtcpRef.current,
      tcpRef.current,
    ]
    onSections(sections);
  },[onSections])

  useEffect(() => {
    const cleanUp = root.current;
    root.current.addEventListener("wheel", scroll.move, {passive: false});
    return () => cleanUp.removeEventListener("wheel",scroll.move); //clean-up
  },[scroll,root])

  useEffect(() => {
    setArrowDot(scroll.sections);
  },[scroll.sections,arrowDot])

  return(
    <article className={styles.technology}>
      {/* banner */}
      <section ref={bannerRef} className={styles.banner}>
        <h1 className={styles.mainTitle}> <br/></h1>
        <h3 className={styles.subTitle}>   ,<br/>         .</h3>
      </section>
      {/* Technology */}
      <section ref={techRef} className={styles.technologies}>
        <h1 className={styles.technologyTitle}>Technology</h1>
        {/* Istack */}
        <section ref={istackRef} className={styles.textBox}>
        {flag >= 1 ?
          <div className={styles.text}>
            <div className={styles.Box}>
              <h2 className={styles.title}>iStack</h2>
              <h3 className={styles.description}>iSTACK    Global First 
                TCP-IP  .    <br/>     
                     .
              </h3>
              <ul className={styles.unOrderList}>
                <li className={styles.list}>  (HTTP, FTP, VOLP, LTE, SIP, RTP)</li>
                <li className={styles.list}>   (WIFI, WIBRO, WIMAX, GPRS, WCDMA, EVDO)</li>
                <li className={styles.list}>  (     IP   )</li>
              </ul>
            </div>
            <img className={styles.image} src="img/istack.png" alt="istack" /> 
          </div>
          : 
          <div className={styles.text1}>
            <div className={styles.Box}>
              <h2 className={styles.title1}>iStack</h2>
              <h3 className={styles.description1}>iSTACK    Global First 
                TCP-IP  .    <br/>     
                     .
              </h3>
              <ul className={styles.unOrderList1}>
                <li className={styles.list1}>  (HTTP, FTP, VOLP, LTE, SIP, RTP)</li>
                <li className={styles.list1}>   (WIFI, WIBRO, WIMAX, GPRS, WCDMA, EVDO)</li>
                <li className={styles.list1}>  (     IP   )</li>
              </ul>
            </div>
            <img className={styles.image1} src="img/istack.png" alt="istack" /> 
          </div>
          }
          
        </section>
        {/* Virtualization */}
        <section ref={virtualRef} className={styles.textBox}>
        {flag >= 2 ? 
          <div className={styles.text}>
            <img className={styles.image}src="img/virtualization.png" alt="Virtualization" />
            <div className={styles.Box}>
              <h2 className={styles.title}>Network Virtualization</h2>
              <h3 className={styles.description}>iSTACK           .</h3>
              <ul className={styles.unOrderList}>
                <li className={styles.list}>: iStack       .</li>
                <li className={styles.list}>:             .</li>
                <li className={styles.list}> : iStack P2P                 .</li>
                <li className={styles.list}>  : iStack P2P   NAT        NAT      
                       .
                </li>
              </ul>
            </div>
          </div>
          :
          <div className={styles.text1}>
          <img className={styles.image1}src="img/virtualization.png" alt="Virtualization" />
          <div className={styles.Box}>
            <h2 className={styles.title1}>Network Virtualization</h2>
            <h3 className={styles.description1}>iSTACK           .</h3>
            <ul className={styles.unOrderList1}>
              <li className={styles.list1}>: iStack       .</li>
              <li className={styles.list1}>:             .</li>
              <li className={styles.list1}> : iStack P2P                 .</li>
              <li className={styles.list1}>  : iStack P2P   NAT        NAT      
                     .
              </li>
            </ul>
          </div>
        </div>}  
        </section>
        {/* Clean WiFI */}
        <section ref={wifiRef} className={styles.textBox}>
          {flag >= 3 ?
          <div className={styles.text}>
            <div className={styles.Box}>
              <h2 className={styles.title}>Clean WiFi</h2>
              <h3 className={styles.description}>Clean WiFi  . QoS       <br/>        .</h3>
              <ul className={styles.unOrderList}>
                <li className={styles.list}>Clean WiFi               .
                    Clean WiFi WTCP    60%   WiFi          
                                  .
                </li>
              </ul>
            </div>
            <img className={styles.image}src="img/clean.png" alt="Clean" />
          </div>
          : 
          <div className={styles.text1}>
            <div className={styles.Box}>
              <h2 className={styles.title1}>Clean WiFi</h2>
              <h3 className={styles.description1}>Clean WiFi  . QoS       <br/>        .</h3>
              <ul className={styles.unOrderList1}>
                <li className={styles.list1}>Clean WiFi               .
                    Clean WiFi WTCP    60%   WiFi          
                                  .
                </li>
              </ul>
            </div>
            <img className={styles.image1}src="img/clean.png" alt="Clean" />
          </div>
          }
        </section>
        {/* WTCP */}
        <section ref={wtcpRef} className={styles.textBox}>
          {flag >= 4 ? 
          <div className={styles.text}>
            <img className={styles.image}src="img/TDMA.png" alt="TDMA" />
            <div className={styles.Box}>
              <h2 className={styles.title}>IP-TDMA Technology</h2>
              <h3 className={styles.description}>WiFi-QoS     </h3>
              <ul className={styles.unOrderList}>
                <li className={styles.list}>    QoS    </li>
                <li className={styles.list}>WiFi   </li>
                <li className={styles.list}>      </li>
                <li className={styles.list}>  :        </li>
                <li className={styles.list}> :        </li>
                <li className={styles.list}>WiFi   AV(-)  , (   )    </li>
              </ul>
            </div>
          </div>
          : 
          <div className={styles.text1}>
            <img className={styles.image1}src="img/TDMA.png" alt="TDMA" />
            <div className={styles.Box}>
              <h2 className={styles.title1}>IP-TDMA Technology</h2>
              <h3 className={styles.description1}>WiFi-QoS     </h3>
              <ul className={styles.unOrderList1}>
                <li className={styles.list1}>    QoS    </li>
                <li className={styles.list1}>WiFi   </li>
                <li className={styles.list1}>      </li>
                <li className={styles.list1}>  :        </li>
                <li className={styles.list1}> :        </li>
                <li className={styles.list1}>WiFi   AV(-)  , (   )    </li>
              </ul>
            </div>
          </div>
          }
        </section>
        {/* TCP */}
        <section ref={tcpRef} className={styles.textBox}>
          {flag >= 5 ? 
          <div className={styles.text}>
          <div className={styles.Box}>
            <h2 className={styles.title}>TCP Acceleration Technology</h2>
            <h3 className={styles.description}>  WTCP        <br/>        .</h3>
            <ul className={styles.unOrderList}>
              <li className={styles.list}> TCP    TCP     (GGSN  )     TCP   2 .
                NexCon Telecom  3          WTCP        20%   .
              </li>
            </ul>
          </div>  
          <img className={styles.image}src="img/WTCP.png" alt="WTCP" />
          </div>
          :
          <div className={styles.text1}>
          <div className={styles.Box}>
            <h2 className={styles.title1}>TCP Acceleration Technology</h2>
            <h3 className={styles.description1}>  WTCP        <br/>        .</h3>
            <ul className={styles.unOrderList1}>
              <li className={styles.list1}> TCP    TCP     (GGSN  )     TCP   2 .
                NexCon Telecom  3          WTCP        20%   .
              </li>
            </ul>
          </div>  
          <img className={styles.image1}src="img/WTCP.png" alt="WTCP" />
          </div>}
        </section>
      </section>
      {arrowDot? <SideScroller scroll={scroll} arrowDot={arrowDot} flag={flag}/> : ""}
    </article>
  )
}

export default memo(Technology);